import React, {FC, useEffect} from 'react';
import {Link, useNavigate} from "react-router-dom";

import {CartItemType} from "../../../interface";
import {cartsActions} from "../../../redux";
import {useAppDispatch, useAppSelector, useRefs} from "../../../hooks";
import {cartsService, productsService, uriService} from "../../../services";
import {MainButton, PrimaryButton} from "../..";

const HeaderCart: FC = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const {getCurrencyText} = useRefs();
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {cart} = useAppSelector(state => state.cartsReducer);

    useEffect(() => {
        dispatch(cartsActions.loadCart({encode: i18n.encode}));
    }, [dispatch, i18n.encode]);

    const onDeleteItem = (e: React.MouseEvent<HTMLButtonElement>, item: CartItemType) => {
        dispatch(cartsActions.deleteCartItem({encode: i18n.encode, item}));
        e.preventDefault();
    };

    return (
        <>
            {/* <!-- Cart --> */}
            {cart && cart.items.length ? <li className="header-cart dropdown default-dropdown">
                <div className="dropdown-toggle" data-toggle="dropdown" aria-expanded="true">
                    <div className="header-btns-icon">
                        <i className="fa fa-shopping-cart"></i>
                        <span className="qty">{cartsService.getTotalCount(cart)}</span>
                    </div>
                    <div>
                        <strong className="text-uppercase">{i18n.value.MY_CART}:</strong>
                        <br/>
                        <span>{getCurrencyText(cartsService.getTotalPrice(cart))}</span>
                    </div>
                </div>
                <div className="custom-menu">
                    <div id="shopping-cart">
                        <div className="shopping-cart-list">
                            {cart.items.map(item =>
                                <div className="product product-widget" key={item.id}>
                                    <div className="product-thumb">
                                        {item.product.image.detail.length ?
                                            <img src={item.product.image.detail[0].thumb} alt=""/> : null}
                                    </div>
                                    <div className="product-body">
                                        <h3 className="product-price">
                                            {getCurrencyText(productsService.getFinalPrice(item.product))}
                                            <span className="qty">x{item.quantity}</span>
                                        </h3>
                                        <h2 className="product-name">
                                            <Link to={uriService.uriProductById(item.product.id)}>{item.product.name}</Link>
                                        </h2>
                                    </div>
                                    <button className="cancel-btn" onClick={e => onDeleteItem(e, item)}>
                                        <i className="fa fa-trash"></i>
                                    </button>
                                </div>
                            )}
                        </div>
                        <div className="shopping-cart-btns">
                            <MainButton onClick={() => navigate(uriService.uriCart())}>{i18n.value.VIEW_CART}</MainButton>
                            <PrimaryButton onClick={() => navigate(uriService.uriCheckout())}>{i18n.value.CHECKOUT} <i
                                className="fa fa-arrow-circle-right"></i>
                            </PrimaryButton>
                        </div>
                    </div>
                </div>
            </li> : null}
            {/* <!-- /Cart --> */}
        </>
    );
};

export {HeaderCart}
