import {ReviewListType, ReviewPostType, ReviewQueryType, ReviewType} from "../interface";
import {reviewsData} from "../data";

export const reviewsService = {
    reviews: [] as Array<ReviewType>,

    async _loadReviews () {
        if (!this.reviews.length) {
            const total_results = 50;
            const data: Array<ReviewType> = [];
            for (let i = 0; i < total_results; i++) {
                const item = reviewsData[i % 6];
                data.push({
                    ...item,
                    id: String(i + 1),
                    changedAt: new Date().toJSON(),
                    createdAt: new Date().toJSON(),
                });
            }
            this.reviews = data;
        }
    },

    async loadReviews(params: ReviewQueryType): Promise<ReviewListType> {
        await this._loadReviews();

        const pageLimit = 3;
        const total_results = this.reviews.length;
        const total_pages = Math.ceil(total_results / pageLimit);
        const page = params.page > total_pages ? total_pages : params.page < 1 ? 1 : params.page;

        const results: Array<ReviewType> = [];

        const startPos = (page - 1) * pageLimit;
        const endPos = Math.min(startPos + pageLimit, total_results);

        for (let i = startPos; i < endPos; i++) {
            const item = this.reviews[i];
            results.push({...item, product: params.product});
        }

        // console.log({
        //     page,
        //     results,
        //     total_pages,
        //     total_results,
        // });

        return {
            page,
            results,
            total_pages,
            total_results,
        };
    },

    async postReview(review: ReviewPostType): Promise<ReviewListType> {
        await this._loadReviews();

        const result: ReviewType = {
            ...review,
            id: String(this.reviews.length + 1),
            createdAt: new Date().toJSON(),
            changedAt: new Date().toJSON(),
        }

        this.reviews.unshift(result);

        return this.loadReviews({product: review.product, page: 1});
    },

}
