import React, {FC, useEffect, useRef, useState} from 'react';
import {Link, useNavigate, useSearchParams} from "react-router-dom";

import {useAppDispatch, useAppSelector, useRefs} from "../../hooks";
import {ProductQuickType, ProductType} from "../../interface";
import {cartsActions, productsActions} from "../../redux";
import {productsService, uriService} from "../../services";
import {MainButton, PrimaryButton, ProductReview, StarsRating} from "..";

interface ProductQuickPropsType {
    fullView?: boolean;
    id: string;
}

const ProductQuick: FC<ProductQuickPropsType> = ({id, fullView}) => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const {product} = useAppSelector(state => state.productsReducer);
    const {refs, getBrandById, getCurrencyText} = useRefs();

    const {i18n} = useAppSelector(state => state.i18nReducer);
    const [reviewCount, setReviewCount] = useState(0);
    const [productCount, setProductCount] = useState(1);

    // const [detailPage, setDetailPage] = useState(0);
    const [params, setParams] = useSearchParams({});

    const mainViewRef = useRef(null);
    const productViewRef = useRef(null);
    const reviewsRef = useRef(null);

    useEffect(() => {
        dispatch(productsActions.getProductById({id, encode: i18n.encode}));
    }, [dispatch, id, i18n.encode]);

    useEffect(() => {
        if (!product?.image.detail) return;

        const {$} = window as any;
        // PRODUCT DETAILS SLICK
        // $('#product-main-view').slick({
        const mainView = $(mainViewRef.current);
        mainView.slick({
            infinite: true,
            speed: 300,
            dots: false,
            arrows: true,
            fade: true,
            asNavFor: '#product-view',
        });

        // $('#product-view').slick({
        const productView = $(productViewRef.current);
        productView.slick({
            slidesToShow: 3,
            slidesToScroll: 1,
            arrows: true,
            centerMode: true,
            focusOnSelect: true,
            asNavFor: '#product-main-view',
        });

        // PRODUCT ZOOM
        $('#product-main-view .product-view').zoom();

        // console.log(product);

        return () => {
            mainView.slick('unslick');
            delete mainView.slick;

            productView.slick('unslick');
            delete productView.slick;
        }

    }, [product]);

    const sizesEl: Array<JSX.Element> = [];
    const colorsEl: Array<JSX.Element> = [];
    const byModel = product?.byModel;
    if (byModel) {
        const productSize = product?.size;
        const productColor = product?.color;
        const onProductSwitchClick = (e: React.MouseEvent<HTMLAnchorElement>, value?: ProductQuickType) => {
            if (value) {
                navigate(uriService.uriProductById(value.id));
            }
            e.preventDefault();
        }

        refs.sizes.forEach(sizeItem => {
            if (productSize === sizeItem.id) {
                sizesEl.push(
                    <li key={product.id} className="active">
                        <a href={uriService.uriProductById(product.id)}
                           onClick={e => onProductSwitchClick(e)}>{sizeItem.name}</a>
                    </li>
                );
            } else {
                const byModelSize = byModel.find(value => value.size === sizeItem.id && value.color === productColor);
                byModelSize &&
                    sizesEl.push(
                        <li key={byModelSize.id}>
                            <a href={uriService.uriProductById(byModelSize.id)}
                               onClick={e => onProductSwitchClick(e, byModelSize)}>{sizeItem.name}</a>
                        </li>
                    );
            }
        });

        refs.colors.forEach(colorItem => {
            if (productColor === colorItem.id) {
                colorsEl.push(
                    <li key={product.id} className="active">
                        <Link to={uriService.uriProductById(product.id)}
                           onClick={e => onProductSwitchClick(e)}
                           style={{backgroundColor: colorItem.color}}></Link>
                    </li>
                );
            } else {
                const byModelColor = byModel.find(value => value.color === colorItem.id && value.size === productSize);
                byModelColor &&
                colorsEl.push(
                    <li key={byModelColor.id}>
                        <Link to={uriService.uriProductById(byModelColor.id)}
                           onClick={e => onProductSwitchClick(e, byModelColor)}
                           style={{backgroundColor: colorItem.color}}></Link>
                    </li>
                );

            }
        });
    }

    let detailPage = Number(params.get("view") || '1');
    if (detailPage < 1 || detailPage > 3) detailPage = 1;
    const setDetailPage = (page: number) => {
        setParams(prevState => {
            prevState.set('view', String(page));
            return prevState;
        });
    }

    const scrollIntoReview = (e: React.MouseEvent<HTMLAnchorElement>) => {
        setDetailPage(3);
        requestAnimationFrame(() => {
            if (!reviewsRef.current) return;
            const form: HTMLFormElement = reviewsRef.current;
            form.scrollIntoView({ block: 'center', behavior: 'smooth' });

            setTimeout(() => {
                form.elements.length && (form.elements[0] as HTMLInputElement).focus();
            }, 200);
        });
        e.preventDefault();
    }

    const onChangeProductCount = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value: number = Number((e.target as HTMLInputElement).value);
        value >= 1 && value <= 10 && setProductCount(value);
    }

    const onClickAddToCart = (product: ProductType, quantity: number) => {
        dispatch(cartsActions.putCartItem({
            encode: i18n.encode,
            product,
            quantity,
            append: true,
        }));
    };

    return (
        <>
            {/* <!-- section --> */}
            {/*<div className="section">*/}
            {/* <!-- container --> */}
            {/*<div className="container">*/}
            {/* <!-- row --> */}
            <div className="row">
                {/* <!--  Product Details --> */}
                <div className="product product-details clearfix">
                    <div className="col-md-6">
                        <div id="product-main-view" ref={mainViewRef}>
                            {product?.image.detail.map((image, index) =>
                                <div key={index} className="product-view">
                                    <img src={image.main} alt=""/>
                                </div>
                            )}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.mainProduct01} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.mainProduct02} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.mainProduct03} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.mainProduct04} alt=""/>*/}
                            {/*</div>*/}
                        </div>
                        <div id="product-view" ref={productViewRef}>
                            {product?.image.detail.map((image, index) =>
                                <div key={index} className="product-view">
                                    <img src={image.thumb} alt=""/>
                                </div>
                            )}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.thumbProduct01} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.thumbProduct02} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.thumbProduct03} alt=""/>*/}
                            {/*</div>*/}
                            {/*<div className="product-view">*/}
                            {/*    <img src={imagesData.thumbProduct04} alt=""/>*/}
                            {/*</div>*/}
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="product-body">
                            <div className="product-label">
                                {product?.isNew ? <span>{i18n.value.NEW}</span> : null}
                                {product?.sale && productsService.hasSale(product) ? <span className="sale">-{product.sale}%</span> : null}
                            </div>
                            <h2 className="product-name">{product?.name}</h2>
                            <h3 className="product-price">
                                {product && getCurrencyText(productsService.getFinalPrice(product))}
                                {product?.sale && productsService.hasSale(product) ?
                                    <>
                                        &nbsp;
                                        <del className="product-old-price">
                                            {getCurrencyText(product.price || 0)}
                                        </del>
                                    </> : null
                                }
                            </h3>
                            <div>
                                <div className="product-rating">
                                    <StarsRating value={product?.rating || 0}/>
                                </div>
                                {fullView &&
                                    <a href="." onClick={scrollIntoReview}>
                                        {reviewCount ? `${reviewCount} ${i18n.value.REVIEWS_N} / ` : ''}
                                        {i18n.value.ADD_REVIEW}
                                    </a>
                                }
                            </div>
                            <p>
                                <strong>{i18n.value.AVAILABLE}:</strong> {product?.quantity ? i18n.value.IN_STOCK : i18n.value.NOT_IN_STOCK}
                            </p>
                            <p>
                                <strong>{i18n.value.BRAND}:</strong> {product?.brand && getBrandById(product.brand)?.name}
                            </p>
                            <p>{product?.brief}</p>
                            <div className="product-options">
                                <ul className="size-option">
                                    <li><span className="text-uppercase">{i18n.value.SIZE}:</span></li>
                                    {sizesEl}
                                    {/*<li className="active"><a href=".">S</a></li>*/}
                                    {/*<li><a href=".">XL</a></li>*/}
                                    {/*<li><a href=".">SL</a></li>*/}
                                </ul>
                                <ul className="color-option">
                                    <li><span className="text-uppercase">{i18n.value.COLOR}:</span></li>
                                    {colorsEl}
                                    {/*<li className="active"><Link to="." style={{backgroundColor: "#475984"}}></Link></li>*/}
                                    {/*<li><Link to="." style={{backgroundColor: "#8A2454"}}></Link></li>*/}
                                    {/*<li><Link to="." style={{backgroundColor: "#BF6989"}}></Link></li>*/}
                                    {/*<li><Link to="." style={{backgroundColor: "#9A54D8"}}></Link></li>*/}
                                </ul>
                            </div>

                            <div className="product-btns">
                                <div className="qty-input">
                                    <span className="text-uppercase">{i18n.value.QTY}: </span>
                                    <input className="input" type="number"
                                           value={productCount} onChange={onChangeProductCount}/>
                                </div>
                                <PrimaryButton className="add-to-cart"
                                               disabled={!product?.quantity || productCount > product?.quantity}
                                               onClick={() => product && onClickAddToCart(product, productCount)}
                                ><i
                                    className="fa fa-shopping-cart"></i> {i18n.value.ADD_TO_CART}</PrimaryButton>
                                <div className="pull-right">
                                    <MainButton className="icon-btn"><i className="fa fa-heart"></i></MainButton>
                                    <MainButton className="icon-btn"><i className="fa fa-exchange"></i></MainButton>
                                    <MainButton className="icon-btn"><i className="fa fa-share-alt"></i></MainButton>
                                </div>
                            </div>
                        </div>
                    </div>
                    {fullView ?
                        <div className="col-md-12">
                            <div className="product-tab">
                                <ul className="tab-nav">
                                    <li className={detailPage === 1 ? "active" : ""}>
                                        <a data-toggle="tab" href="#tab1" onClick={() => setDetailPage(1)}>
                                            {i18n.value.DESCRIPTION}
                                        </a>
                                    </li>
                                    <li className={detailPage === 2 ? "active" : ""}>
                                        <a data-toggle="tab" href="#tab2" onClick={() => setDetailPage(2)}>
                                            {i18n.value.DETAILS}
                                        </a>
                                    </li>
                                    <li className={detailPage === 3 ? "active" : ""}>
                                        <a data-toggle="tab" href="#tab3" onClick={() => setDetailPage(3)}>
                                            {i18n.value.REVIEWS} ({reviewCount})
                                        </a>
                                    </li>
                                </ul>
                                <div className="tab-content">
                                    <div id="tab1" className={"tab-pane fade in " + (detailPage === 1 ? "active" : "")}>
                                        <p>{product?.description}</p>
                                    </div>

                                    <div id="tab2" className={"tab-pane fade in " + (detailPage === 2 ? "active" : "")}>
                                        <p>{product?.details}</p>
                                    </div>

                                    <div id="tab3" className={"tab-pane fade in " + (detailPage === 3 ? "active" : "")}>
                                        {product &&
                                            <ProductReview product={product}
                                                           onTotal={setReviewCount}
                                                           ref={reviewsRef}
                                            />
                                        }
                                    </div>
                                </div>
                            </div>
                        </div> : null
                    }
                </div>
                {/* <!-- /Product Details --> */}
            </div>
            {/* <!-- /row --> */}
            {/*</div>*/}
            {/* <!-- /container --> */}
            {/*</div>*/}
            {/* <!-- /section --> */}
        </>
    );
};

export {ProductQuick}
