import React from 'react';
import {SubmitHandler} from "react-hook-form/dist/types/form";
import {useNavigate, useNavigationType} from "react-router-dom";

import {OrderPostType, UserLoginProps} from "../../interface";
import {uriService} from "../../services";
import {ordersActions} from "../../redux";
import {useAppDispatch, useAppSelector} from "../../hooks";
import {Breadcrumb, UserForm} from "../../components";

const CheckoutPage: React.FC = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const navType = useNavigationType();
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {cart} = useAppSelector(state => state.cartsReducer);
    const {user} = useAppSelector(state => state.usersReducer);
    const onSubmit: SubmitHandler<UserLoginProps> = async (postUser) => {
        if (!cart) return;

        const order: OrderPostType = {
            user: user?.id || postUser,
            zip_code: postUser.zip_code,
            country: postUser.country,
            city: postUser.city,
            address: postUser.address,
            shipping: postUser.shipping,
            payments: postUser.payments,
            cart: cart.id
        };

        dispatch(ordersActions.createOrder({encode: i18n.encode, order})).then(
            () => {
                if (navType === 'PUSH')
                    navigate(-1)
                else
                    navigate(uriService.uriProducts(), {replace: true})
            }
        );
    };

    return (
        <>
            <Breadcrumb active={i18n.value.CHECKOUT}/>
            {/* <!-- section --> */}
            <div className="section">
                {/* <!-- container --> */}
                <div className="container">
                    {/* <!-- row --> */}
                    <div className="row">
                        <UserForm user={user} makeOrder={true} onSubmit={onSubmit}/>
                    </div>
                    {/* <!-- /row --> */}
                </div>
                {/* <!-- /container --> */}
            </div>
            {/* <!-- /section --> */}
        </>
    );
};

export {CheckoutPage}
