import React, {FC} from 'react';
import {useNavigate} from "react-router-dom";

import {useAppSelector, useRefs} from "../../../hooks";
import {uriService} from "../../../services";
import {SectionBanner} from "../../SectionBanner";

const NewCollectionSection: FC = () => {
    const navigate = useNavigate();
    const {summary} = useAppSelector(state => state.summaryReducer);
    const {getCollectionById} = useRefs();
    return (
        <>
            {/* <!-- NewCollection --> */}
            <div className="section">
                {/* <!-- container --> */}
                <div className="container">
                    {/* <!-- row --> */}
                    <div className="row">
                        {summary?.home.collectionNormal.map(collectionId => {
                            const collection = getCollectionById(collectionId);

                            if (!collection || !collection.banner.normal) return null;

                            return (
                                <div className="col-md-4 col-sm-6" key={collectionId}>
                                    {/* <!-- banner --> */}
                                    <SectionBanner
                                        className="banner-1"
                                        image={collection.banner.normal}
                                        centered={true}
                                        onClick={()=>{
                                            navigate(uriService.uriProductsByCollectionId(collection.id));
                                        }}>
                                        <h2 className="white-color">{collection.name}</h2>
                                    </SectionBanner>
                                    {/* <!-- /banner --> */}
                                </div>
                            )
                        })}
                    </div>
                    {/* <!-- /row --> */}
                </div>
                {/* <!-- /container --> */}
            </div>
            {/* <!-- /NewCollection --> */}
        </>
    );
};

export {NewCollectionSection}
