import React, {FC} from 'react';
import {Link} from "react-router-dom";

import {StarsRating} from "..";
import {ProductType} from "../../interface";
import {productsService, uriService} from "../../services";
import {useRefs} from "../../hooks";

const ProductWidget: FC<ProductType> = (props) => {
    const {getCurrencyText} = useRefs();
    return (
        <>
            {/* <!-- widget product --> */}
            <div className="product product-widget">
                <div className="product-thumb">
                    <img src={props.image.column} alt=""/>
                </div>
                <div className="product-body">
                    <h2 className="product-name"><Link to={uriService.uriProductById(props.id)}>{props.name}</Link></h2>
                    <h3 className="product-price">
                        {getCurrencyText(productsService.getFinalPrice(props))}
                        {productsService.hasSale(props) ?
                            <>
                                &nbsp;
                                <del className="product-old-price">
                                    {getCurrencyText(props.price)}
                                </del>
                            </> : null
                        }
                    </h3>
                    <div className="product-rating">
                        <StarsRating value={props.rating || 0}/>
                    </div>
                </div>
            </div>
            {/* <!-- /widget product --> */}
        </>
    );
};

export {ProductWidget}
