import {cartsData, cartItemsData} from "../data";
import {CartItemType, CartType, I18nLocale, ProductType} from "../interface";
import {productsService} from "./products.service";

export const cartsService = {
    cart: undefined as CartType | undefined,
    encode: undefined as I18nLocale | undefined,

    async resetCart(encode: I18nLocale): Promise<CartType> {
        this.cart = {
            items: [],
            id: 'NEW',
            createdAt: "2023-03-02T16:28:00.000Z",
            changedAt: "2023-03-02T16:28:00.000Z",
        }
        this.encode = encode;

        return this.cart as CartType;
    },

    async loadCart(encode: I18nLocale): Promise<CartType> {
        if (this.cart && this.encode === encode) {
            return this.cart;
        }
        const cart = cartsData[0];
        const cartItems = cartItemsData.filter(item => item.cart === cart.id);
        const products = await Promise.all(cartItems.map(item => productsService.getProductById(item.product, encode)));

        const items: Array<CartItemType> = [];

        cartItems.forEach(item => {
            const product = products.find(product => product?.id === item.product);
            product && items.push({...item, product});
        });

        this.cart = {...cart, items}
        this.encode = encode;

        return this.cart;
    },

    async deleteItem(encode: I18nLocale, item: CartItemType): Promise<CartType> {
        const cart = await this.loadCart(encode);
        const items = cart.items.filter(cartItem => cartItem.id !== item.id);

        this.cart = {...cart, items}
        return this.cart;
    },

    async putProduct(encode: I18nLocale, product: ProductType, quantity: number, append: boolean): Promise<CartType> {

        const cart = await this.loadCart(encode);
        const items: Array<CartItemType> = [];
        let found = false;

        cart.items.forEach(cartItem => {
            if (cartItem.product.id === product.id) {
                found = true;
                if (append && cartItem.quantity + quantity > 0) {
                    items.push({...cartItem, quantity: cartItem.quantity + quantity});
                } else if (!append && quantity > 0) {
                    items.push({...cartItem, quantity});
                }
            } else {
                items.push({...cartItem});
            }
        });

        if (!found && quantity > 0) {
            const productDb = await productsService.getProductById(product.id, encode);
            if (productDb) {
                items.push({
                    id: String(cart.items.length + 1),
                    cart: cart.id,
                    product: productDb,
                    quantity,
                    createdAt: new Date().toJSON(),
                    changedAt: new Date().toJSON(),
                });
            }
        }

        this.cart = {...cart, items}
        return this.cart;
    },

    getTotalPrice(cart: CartType): number {
        return cart.items.reduce((total, item) => {
            total += item.quantity * productsService.getFinalPrice(item.product);
            return total;
        }, 0);
    },

    getTotalCount(cart: CartType): number {
        return cart.items.reduce((total, item) => {
            total += item.quantity;
            return total;
        }, 0);
    },
}
