import React, {useEffect} from 'react';
import {SubmitHandler} from "react-hook-form/dist/types/form";
import {Link} from "react-router-dom";
import {useForm} from "react-hook-form";
import {joiResolver} from "@hookform/resolvers/joi/dist/joi";

import {UserType, UserLoginProps} from "../../interface";
import {uriService} from "../../services";
import {useAppSelector, useRefs} from "../../hooks";
import {userValidator} from "../../validators";
import {FieldErrorMessage, OrderSummary, PrimaryButton} from "..";
import {PaymentMethodEnum, ShippingMethodEnum} from "../../enum";

interface UserFormPropsType {
    makeOrder?: boolean;
    user?: UserType;
    onSubmit: SubmitHandler<UserLoginProps>;
}

const defaultUserFormPostType: Partial<UserLoginProps> = {
    shipping: ShippingMethodEnum.freeShipping,
    payments: PaymentMethodEnum.bankTransfer,
}

const UserForm: React.FC<UserFormPropsType> = ({makeOrder, user, onSubmit}) => {
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {cart} = useAppSelector(state => state.cartsReducer);
    const {refs, getCurrencyText} = useRefs();
    const {register, handleSubmit, reset, formState: {errors, isValid}, setValue} = useForm<UserLoginProps>({
        resolver: joiResolver(userValidator),
        mode: 'all'
    });

    useEffect(() => {
        reset(defaultUserFormPostType);
    }, [reset]);

    useEffect(() => {
        if (user) {

            setValue('first_name', user.first_name);
            setValue('last_name', user.last_name);
            setValue('email', user.email);
            setValue('address', user.address);
            setValue('city', user.city);
            setValue('zip_code', user.zip_code);
            setValue('tel', user.tel);
            setValue('country', user.country);
            setValue('shipping', user.shipping);
            setValue('payments', user.payments);

            setValue('register', false);
            setValue('password', '');
            setValue('passwordConfirm', '');

        }
    }, [setValue, user]);


    return (
        <form id="checkout-form" className="clearfix" onSubmit={handleSubmit(onSubmit)}>
            <div className="col-md-6">
                <div className="billing-details">
                    {!user && makeOrder ?
                        <p>
                            {i18n.value.ALREADY_CUSTOMER_QUESTION}&nbsp;
                            <Link to={uriService.uriLogin()}>{i18n.value.LOGIN}</Link>
                        </p> : null
                    }
                    <div className="section-title">
                        <h3 className="title">{i18n.value[makeOrder ? "BILLING_DETAILS" : "USER_DETAILS"]}</h3>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               readOnly={!!user}
                               placeholder={i18n.value.FORM_FIRST_NAME}
                               {...register('first_name')}
                        />
                        <FieldErrorMessage error={errors['first_name']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               readOnly={!!user}
                               placeholder={i18n.value.FORM_LAST_NAME}
                               {...register('last_name')}
                        />
                        <FieldErrorMessage error={errors['last_name']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="email"
                               readOnly={!!user}
                               placeholder={i18n.value.FORM_EMAIL}
                               {...register('email')}
                        />
                        <FieldErrorMessage error={errors['email']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               placeholder={i18n.value.FORM_ADDRESS}
                               {...register('address')}
                        />
                        <FieldErrorMessage error={errors['address']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               placeholder={i18n.value.FORM_CITY}
                               {...register('city')}
                        />
                        <FieldErrorMessage error={errors['city']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               placeholder={i18n.value.FORM_COUNTRY}
                               {...register('country')}
                        />
                        <FieldErrorMessage error={errors['country']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="text"
                               placeholder={i18n.value.FORM_ZIP_CODE}
                               {...register('zip_code')}
                        />
                        <FieldErrorMessage error={errors['zip_code']}/>
                    </div>
                    <div className="form-group">
                        <input className="input" type="tel"
                               placeholder={i18n.value.FORM_TELEPHONE}
                               {...register('tel')}
                        />
                        <FieldErrorMessage error={errors['tel']}/>
                    </div>
                    {!makeOrder || !user ?
                        <div className="form-group">
                            <div className="input-checkbox">
                                <input type="checkbox" id="register"
                                       {...register('register')}
                                />
                                <label className="font-weak form-group" htmlFor="register">
                                    {i18n.value[makeOrder ? "NEW_ACCOUNT_QUESTION" : "CHANGE_PASSWORD_QUESTION"]}
                                </label>
                                <div className="caption">
                                    {!user ?
                                        <p>{i18n.value.ENTER_PASSWORD_INFO}</p> :
                                        <p>{i18n.value.RE_ENTER_PASSWORD_INFO}</p>
                                    }
                                    <div className="form-group">
                                        <input className="input" type="password"
                                               readOnly={!!user}
                                               disabled={!!user}
                                               placeholder={i18n.value.ENTER_YOUR_PASSWORD}
                                               {...register('password')}
                                        />
                                        <FieldErrorMessage error={errors['password']}/>
                                    </div>
                                    <div className="form-group">
                                        <input className="input" type="password"
                                               readOnly={!!user}
                                               disabled={!!user}
                                               placeholder={i18n.value.REENTER_YOUR_PASSWORD}
                                               {...register('passwordConfirm')}
                                        />
                                        <FieldErrorMessage error={errors['passwordConfirm']}/>
                                    </div>
                                </div>
                            </div>
                        </div> : null
                    }
                </div>
            </div>

            <div className="col-md-6">
                <div className="shiping-methods">
                    <div className="section-title">
                        <h4 className="title">{i18n.value.SHIPPING_METHODS}</h4>
                    </div>
                    {refs.shipping.map(method =>
                        <div className="input-checkbox" key={method.id}>
                            <input type="radio"
                                   id={`shipping-${method.id}`}
                                   value={method.id}
                                   {...register('shipping')}
                            />
                            <label htmlFor={`shipping-${method.id}`}>
                                {method.name + (method.price ? ' - ' + getCurrencyText(method.price) : '')}
                            </label>
                            <div className="caption">
                                <p>{method.info}</p>
                            </div>
                        </div>
                    )}
                    <FieldErrorMessage error={errors['shipping']}/>
                </div>

                <div className="payments-methods">
                    <div className="section-title">
                        <h4 className="title">{i18n.value.PAYMENT_METHODS}</h4>
                    </div>

                    {refs.payments.map(method =>
                        <div className="input-checkbox" key={method.id}>
                            <input type="radio"
                                   id={`payments-${method.id}`}
                                   value={method.id}
                                   {...register('payments')}
                            />
                            <label htmlFor={`payments-${method.id}`}>{method.name}</label>
                            <div className="caption">
                                <p>{method.info}</p>
                            </div>
                        </div>
                    )}
                    <FieldErrorMessage error={errors['payments']}/>
                </div>
            </div>

            <div className="col-md-12">
                {makeOrder ?
                    <OrderSummary cart={cart}
                                  makeOrder={true}
                                  onClick={() => handleSubmit(onSubmit)}
                                  onCheckValid={() => isValid}/> :
                    <div className="pull-right">
                        <PrimaryButton onClick={() => handleSubmit(onSubmit)} disabled={!isValid}>
                            {i18n.value.SUBMIT}
                        </PrimaryButton>
                    </div>
                }
            </div>

        </form>
    );
};

export {UserForm}
