import {createAsyncThunk, createSlice} from "@reduxjs/toolkit";
import {AxiosError} from "axios";

import {ProductQueryType, ProductListType, ProductDetailType, I18nLocale} from "../../interface";
import {productsService} from "../../services";

interface ProductListStateType {
    products: ProductListType;
    product: ProductDetailType;
    loading: boolean;
    error: string;
}

const initialState: Partial<ProductListStateType> = {}

const loadProducts = createAsyncThunk<ProductListType, ProductQueryType>(
    'productsSlice/loadProducts',
    async (params, {rejectWithValue}) => {
        try {
            return productsService.loadProducts(params);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const getProductById = createAsyncThunk<ProductDetailType | null, { id: string, encode: I18nLocale }>(
    'productsSlice/getProductById',
    async (props, {rejectWithValue}) => {
        try {
            return productsService.getProductDetailById(props.id, props.encode);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const productsSlice = createSlice({
    name: 'productsSlice',
    initialState,
    reducers: {},
    extraReducers: builder =>
        builder
            // .addCase(loadProducts.pending, (state) => {
            //     state.error = null;
            //     state.loading = true;
            // })
            // .addCase(loadProducts.rejected, (state, action) => {
            //     state.error = action.payload as string;
            //     state.loading = false;
            // })
            .addCase(loadProducts.fulfilled, (state, action) => {
                const products = action.payload as ProductListType;
                state.products = {...products};
                state.loading = false;
            })
            .addCase(getProductById.fulfilled, (state, action) => {
                const product = action.payload;
                state.product = product ? {...product} : undefined;
                state.loading = false;
            })
            .addDefaultCase((state, action) => {
                const [pathElement] = action.type.split('/').splice(-1);
                if (pathElement === 'rejected') {
                    state.error = action.payload as string;
                    state.loading = false;
                } else if (pathElement === 'pending') {
                    state.error = undefined;
                    state.loading = true;
                } else {
                    state.error = undefined;
                }
            })
});

const {reducer: productsReducer} = productsSlice;

const productsActions = {
    loadProducts,
    getProductById,
}

export {productsReducer, productsActions}
