import React, {FC} from 'react';
import {useNavigate} from "react-router-dom";

import {useAppSelector, useRefs} from "../../../../hooks";
import {uriService} from "../../../../services";
import {SectionTitle, ProductSingle, SectionBanner, PrimaryButton} from "../../..";

const LatestProducts: FC = () => {
    const navigate = useNavigate();
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {summary} = useAppSelector(state => state.summaryReducer);
    const {getCollectionById} = useRefs();
    const collection = summary?.latest.collectionColumn ? getCollectionById(summary?.latest.collectionColumn) : null;
    return (
        <>
            {/* <!-- Latest Products --> */}
            {/* <!-- row --> */}
            <div className="row">
                <div className="col-md-12">
                    <SectionTitle caption={i18n.value.LATEST_PRODUCTS}/>
                </div>

                {[0,1,2,3].map(value =>
                    summary?.latest.products[value] ?
                        <div key={value} className="col-md-3 col-sm-6 col-xs-6">
                            <ProductSingle product={summary?.latest.products[value]}/>
                        </div> : null
                )}
            </div>
            {/* <!-- /row --> */}

            {/* <!-- row --> */}
            <div className="row">
                <div className="col-md-3 col-sm-6 col-xs-6">
                    {collection && collection.banner.column ?
                        <SectionBanner className="banner-2" image={collection.banner.column}>
                            <h2 className="white-color">{collection.name}</h2>
                            <PrimaryButton onClick={()=>{
                                navigate(uriService.uriProductsByCollectionId(collection.id));
                            }}>{i18n.value.SHOP_NOW}</PrimaryButton>
                        </SectionBanner> : null
                    }
                </div>

                {[4,5,6].map(value =>
                    summary?.latest.products[value] ?
                        <div key={value} className="col-md-3 col-sm-6 col-xs-6">
                            <ProductSingle product={summary?.latest.products[value]}/>
                        </div> : null
                )}
            </div>
            {/* <!-- /row --> */}
            {/* <!-- /LatestProducts --> */}
        </>
    );
};

export {LatestProducts}
