import {createAsyncThunk, createSlice} from "@reduxjs/toolkit";
import {AxiosError} from "axios";

import {ApiKey, I18nLocale, UserLoginProps, UserType} from "../../interface";
import {usersService} from "../../services";

export interface UsersStateType {
    user: UserType;
    loading: boolean;
    error: string;
}

const initialState: Partial<UsersStateType> = {}

const createUser = createAsyncThunk<UserType, {encode: I18nLocale; user: UserLoginProps }> (
    'usersSlice/createUser',
    async ({encode, user}, {rejectWithValue, dispatch}) => {
        try {
            return await usersService.createUser(encode, user);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).message)
            }
        }
    }
);

const updateUser = createAsyncThunk<UserType, {encode: I18nLocale; id: ApiKey; user: UserLoginProps }> (
    'usersSlice/updateUser',
    async ({encode, id, user}, {rejectWithValue, dispatch}) => {
        try {
            return await usersService.updateUser(encode, id, user);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).message)
            }
        }
    }
);

const loginUser = createAsyncThunk<UserType, {encode: I18nLocale; email: string; password: string }> (
    'usersSlice/loginUser',
    async ({encode, email, password}, {rejectWithValue, dispatch}) => {
        try {
            return await usersService.loginUser(encode, email, password);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).message)
            }
        }
    }
);

const usersSlice = createSlice({
    name: 'usersSlice',
    initialState,
    reducers: {
        logoutUser: (state) => {
            state.user = undefined;
        },
    },
    extraReducers: builder =>
        builder
            .addCase(createUser.fulfilled, (state, action) => {
                const user = action.payload as UserType;
                state.user = {...user};
                state.loading = false;
            })
            .addCase(updateUser.fulfilled, (state, action) => {
                const user = action.payload as UserType;
                state.user = {...user};
                state.loading = false;
            })
            .addCase(loginUser.fulfilled, (state, action) => {
                const user = action.payload as UserType;
                state.user = {...user};
                state.loading = false;
            })
            .addDefaultCase((state, action) => {
                const [pathElement] = action.type.split('/').splice(-1);
                if (pathElement === 'rejected') {
                    state.error = action.payload as string;
                    state.loading = false;
                } else if (pathElement === 'pending') {
                    state.error = undefined;
                    state.loading = true;
                } else {
                    state.error = undefined;
                }
            })
});

const {reducer: usersReducer, actions: {logoutUser}} = usersSlice;

const usersActions = {
    createUser,
    updateUser,
    loginUser,
    logoutUser,
}

export {usersReducer, usersActions}
