import React from 'react';
import {useNavigate, useNavigationType} from "react-router-dom";
import {SubmitHandler} from "react-hook-form/dist/types/form";

import {UserLoginProps} from "../../interface";
import {uriService} from "../../services";
import {usersActions} from "../../redux";
import {useAppDispatch, useAppSelector} from "../../hooks";
import {Breadcrumb, UserForm} from "../../components";

const AccountPage: React.FC = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const navType = useNavigationType();
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {user} = useAppSelector(state => state.usersReducer);

    const onSubmit: SubmitHandler<UserLoginProps> = async (postUser) => {
        if (!user) return;
        dispatch(usersActions.updateUser({encode: i18n.encode, id: user.id, user: postUser})).then(
            () => {
                if (navType === 'PUSH')
                    navigate(-1)
                else
                    navigate(uriService.uriProducts(), {replace: true})
            }
        );
    };

    return (
        <>
            <Breadcrumb active={i18n.value.MY_ACCOUNT}/>
            {/* <!-- section --> */}
            <div className="section">
                {/* <!-- container --> */}
                <div className="container">
                    {/* <!-- row --> */}
                    <div className="row">
                        <UserForm user={user} onSubmit={onSubmit}/>
                    </div>
                    {/* <!-- /row --> */}
                </div>
                {/* <!-- /container --> */}
            </div>
            {/* <!-- /section --> */}
        </>
    );
};

export {AccountPage}
