import {createAsyncThunk, createSlice} from "@reduxjs/toolkit";
import {AxiosError} from "axios";

import {ReviewListType, ReviewPostType, ReviewQueryType} from "../../interface";
import {reviewsService} from "../../services";

interface ReviewListStateType {
    reviews: ReviewListType;
    loading: boolean;
    error: string;
}

const initialState: Partial<ReviewListStateType> = {}

const loadReviews = createAsyncThunk<ReviewListType, ReviewQueryType>(
    'reviewsSlice/loadReviews',
    async (params, {rejectWithValue}) => {
        try {
            return reviewsService.loadReviews(params);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const postReview = createAsyncThunk<ReviewListType, ReviewPostType>(
    'reviewsSlice/postReview',
    async (review, {rejectWithValue}) => {
        try {
            return reviewsService.postReview(review);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const reviewsSlice = createSlice({
    name: 'reviewsSlice',
    initialState,
    reducers: {},
    extraReducers: builder =>
        builder
            .addCase(loadReviews.fulfilled, (state, action) => {
                const reviews = action.payload as ReviewListType;
                state.reviews = {...reviews}
                state.loading = false;
            })
            .addCase(postReview.fulfilled, (state, action) => {
                const reviews = action.payload as ReviewListType;
                state.reviews = {...reviews}
                state.loading = false;
            })
            .addDefaultCase((state, action) => {
                const [pathElement] = action.type.split('/').splice(-1);
                if (pathElement === 'rejected') {
                    state.error = action.payload as string;
                    state.loading = false;
                } else if (pathElement === 'pending') {
                    state.error = undefined;
                    state.loading = true;
                } else {
                    state.error = undefined;
                }
            })
});

const {reducer: reviewsReducer} = reviewsSlice;

const reviewsActions = {
    loadReviews,
    postReview,
}

export {reviewsReducer, reviewsActions}
