import {
    ProductQueryType,
    ProductListType,
    ProductType,
    ProductQuickType,
    I18nLocale,
    ProductDetailType
} from "../interface";
import {productsData} from "../data";
import {PageTakeEnum} from "../enum";


export const productsService = {
    products: [] as Array<ProductType>,
    encode: undefined as I18nLocale | undefined,

    async _loadProducts(encode: I18nLocale) {
        if (!this.products.length || encode !== this.encode) {
            const total_results = 200;
            const data: Array<ProductType> = [];
            for (let i = 0; i < total_results; i++) {
                const item = productsData[i % 9];
                data.push({
                    ...item,
                    size: String((i % 8) + 1),
                    color: String((i % 7) + 1),
                    id: String(i + 1),
                    name: item.name[encode],
                    image: {
                        column: item.image.column,
                        detail: [...item.image.detail]
                    },
                    brief: item.brief[encode],
                    description: item.description[encode],
                    details: item.details[encode],
                });
            }
            this.products = data;
            this.encode = encode;
        }
    },

    async getProductById(id: string, encode: I18nLocale): Promise<ProductType | null> {
        await this._loadProducts(encode);

        return this.products.find(value => value.id === id) || null;
    },

    async getProductDetailById(id: string, encode: I18nLocale): Promise<ProductDetailType | null> {
        await this._loadProducts(encode);

        const item = this.products.find(value => value.id === id);

        if (item) {
            const byModel: Array<ProductQuickType> = this.products
                .filter(value => value.model === item.model)
                .map(value => ({
                    id: value.id,
                    name: value.name,
                    color: value.color,
                    size: value.size,
                    quantity: value.quantity,
                    createdAt: value.createdAt,
                    changedAt: value.changedAt,
                })) || [];
            return {...item, byModel};
        }

        return null;
    },

    async loadProducts(params: ProductQueryType): Promise<ProductListType> {
        await this._loadProducts(params.encode);

        const pageLimit = params.take === PageTakeEnum.limit30 ? 30 :
            params.take === PageTakeEnum.limit20 ? 20 : 10;
        const total_results = this.products.length;
        const total_pages = Math.ceil(total_results / pageLimit);
        const page = params.page > total_pages ? total_pages :
            params.page < 1 ? 1 : params.page;

        const results: Array<ProductType> = [];

        const startPos = (page - 1) * pageLimit;
        const endPos = Math.min(startPos + pageLimit, total_results);

        for (let i = startPos; i < endPos; i++) {
            const item = this.products[i];
            results.push({...item});
        }

        console.log({
            page,
            results,
            total_pages,
            total_results,
        });

        return {
            page,
            results,
            total_pages,
            total_results,
        };
    },

    getSaleOffTime(product: ProductType): number {
        if (!product.saleStop || !product.sale) return 0;

        const countDownDate = new Date(product.saleStop).getTime();
        const now = new Date().getTime();
        const distance = countDownDate - now;

        return distance < 0 ? 0 : distance;
    },

    hasSale(product: ProductType): boolean {
        return !!(product.sale && (!product.saleStop || this.getSaleOffTime(product)));
    },

    getFinalPrice(product: ProductType): number {
        return product.sale && this.hasSale(product) ?
            product.price - (product.price * product.sale / 100) : product.price;
    },
}
