import {createAsyncThunk, createSlice} from "@reduxjs/toolkit";
import {AxiosError} from "axios";

import {I18nLocale, OrderPostType, OrderType, UserLoginProps} from "../../interface";
import {ordersService} from "../../services";
import {cartsActions} from "./carts.slice";
import {usersActions, UsersStateType} from "./users.slice";

interface OrdersStateType {
    order: OrderType;
    loading: boolean;
    error: string;
}

const initialState: Partial<OrdersStateType> = {}

const createOrder = createAsyncThunk<OrderType, {encode: I18nLocale; order: OrderPostType }> (
    'ordersSlice/createOrder',
    async ({encode, order}, {rejectWithValue, dispatch, getState}) => {
        try {
            if (typeof order.user === 'object') {
                await dispatch(usersActions.createUser({encode, user: order.user as UserLoginProps }));
                const { usersReducer: state } = getState() as { usersReducer: UsersStateType };
                order.user = state.user.id;
            }

            const resultOrder = await ordersService.createOrder(encode, order);

            dispatch(cartsActions.resetCart({encode}));

            return resultOrder;
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const ordersSlice = createSlice({
    name: 'ordersSlice',
    initialState,
    reducers: {},
    extraReducers: builder =>
        builder
            .addCase(createOrder.fulfilled, (state, action) => {
                const order = action.payload as OrderType;
                state.order = {...order};
                state.loading = false;
            })
            .addDefaultCase((state, action) => {
                const [pathElement] = action.type.split('/').splice(-1);
                if (pathElement === 'rejected') {
                    state.error = action.payload as string;
                    state.loading = false;
                } else if (pathElement === 'pending') {
                    state.error = undefined;
                    state.loading = true;
                } else {
                    state.error = undefined;
                }
            })
});

const {reducer: ordersReducer} = ordersSlice;

const ordersActions = {
    createOrder,
}

export {ordersReducer, ordersActions}
