import {createAsyncThunk, createSlice} from "@reduxjs/toolkit";
import {AxiosError} from "axios";

import {CartItemType, CartType, I18nLocale, ProductType} from "../../interface";
import {cartsService} from "../../services";

interface CartsStateType {
    cart: CartType;
    loading: boolean;
    error: string;
}

const initialState: Partial<CartsStateType> = {}

const loadCart = createAsyncThunk<CartType | null, { encode: I18nLocale }>(
    'cartsSlice/loadCart',
    async ({encode}, {rejectWithValue}) => {
        try {
            return cartsService.loadCart(encode);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const resetCart = createAsyncThunk<CartType | null, { encode: I18nLocale }>(
    'cartsSlice/resetCart',
    async ({encode}, {rejectWithValue}) => {
        try {
            return cartsService.resetCart(encode);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

interface PutCartItemPropsType {
    encode: I18nLocale;
    product: ProductType;
    quantity: number;
    append: boolean;
}

const putCartItem = createAsyncThunk<CartType, PutCartItemPropsType>(
    'cartsSlice/putCartItem',
    async (props, {rejectWithValue}) => {
        try {
            return cartsService.putProduct(props.encode, props.product, props.quantity, props.append);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

interface DeleteCartItemPropsType {
    encode: I18nLocale;
    item: CartItemType;
}

const deleteCartItem = createAsyncThunk<CartType, DeleteCartItemPropsType>(
    'cartsSlice/deleteCartItem',
    async ({encode, item}, {rejectWithValue}) => {
        try {
            return cartsService.deleteItem(encode, item);
        } catch (e) {
            if (e instanceof AxiosError) {
                const error = e as AxiosError;
                return rejectWithValue(error.response?.data || error.toString())
            } else {
                return rejectWithValue((e as Error).toString())
            }
        }
    }
);

const cartsSlice = createSlice({
    name: 'cartsSlice',
    initialState,
    reducers: {},
    extraReducers: builder =>
        builder
            .addCase(loadCart.fulfilled, (state, action) => {
                const cart = action.payload as CartType;
                state.cart = cart ? {...cart} : undefined;
                state.loading = false;
            })
            .addCase(resetCart.fulfilled, (state, action) => {
                const cart = action.payload as CartType;
                state.cart = cart ? {...cart} : undefined;
                state.loading = false;
            })
            .addCase(putCartItem.fulfilled, (state, action) => {
                const cart = action.payload as CartType;
                state.cart = cart ? {...cart} : undefined;
                state.loading = false;
            })
            .addCase(deleteCartItem.fulfilled, (state, action) => {
                const cart = action.payload as CartType;
                state.cart = cart ? {...cart} : undefined;
                state.loading = false;
            })
            .addDefaultCase((state, action) => {
                const [pathElement] = action.type.split('/').splice(-1);
                if (pathElement === 'rejected') {
                    state.error = action.payload as string;
                    state.loading = false;
                } else if (pathElement === 'pending') {
                    state.error = undefined;
                    state.loading = true;
                } else {
                    state.error = undefined;
                }
            })
});

const {reducer: cartsReducer} = cartsSlice;

const cartsActions = {
    loadCart,
    resetCart,
    putCartItem,
    deleteCartItem,
}

export {cartsReducer, cartsActions}
