import {I18nLocale, OrderPostType, OrderType} from "../interface";
import {ordersData} from "../data";

export const ordersService = {
    orders: [] as Array<OrderType>,

    async _loadOrders () {
        if (!this.orders.length) {
            const data: Array<OrderType> = [];
            for (let i = 0; i < ordersData.length; i++) {
                const item = ordersData[i];
                data.push({...item});
            }
            this.orders = data;
        }
    },

    async createOrder(encode: I18nLocale, order: OrderPostType): Promise<OrderType> {
        await this._loadOrders();

        const result: OrderType = {
            ...order,
            id: String(this.orders.length + 1),
            createdAt: new Date().toJSON(),
            changedAt: new Date().toJSON(),
        }

        this.orders.push(result);

        return result;
    }
}
