import React, {useEffect} from 'react';
import {SubmitHandler} from "react-hook-form/dist/types/form";
import {Link, useNavigate, useNavigationType} from "react-router-dom";
import {useForm} from "react-hook-form";
import {joiResolver} from "@hookform/resolvers/joi/dist/joi";

import {uriService} from "../../services";
import {useAppDispatch, useAppSelector} from "../../hooks";
import {loginValidator} from "../../validators";
import {FieldErrorMessage, PrimaryButton} from "..";
import {usersActions} from "../../redux";

interface UserLoginProps {
    email: string;
    password: string;
}

const LoginForm: React.FC = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const navType = useNavigationType();
    const {user, error} = useAppSelector(state => state.usersReducer);
    const {i18n} = useAppSelector(state => state.i18nReducer);
    const {
        register,
        handleSubmit,
        resetField,
        formState: {errors, isValid}
    } = useForm<UserLoginProps>({
        resolver: joiResolver(loginValidator),
        mode: 'all'
    });

    const onSubmit: SubmitHandler<UserLoginProps> = async ({email, password}) => {
        dispatch(usersActions.loginUser({encode: i18n.encode, email, password})).then(() => resetField("password"));
    };

    useEffect(() => {
        if (user) {
            console.log('LOGGED!!! >> ', user);
            if (navType === 'PUSH')
                navigate(-1)
            else
                navigate(uriService.uriProducts(), {replace: true})
        }
    }, [navType, navigate, user])

    return (
        <div className="col-md-6 col-md-offset-3">
            <form id="login-form" className="clearfix" onSubmit={handleSubmit(onSubmit)}>

                <div className="billing-details">
                    <div className="section-title">
                        <h3 className="title">{i18n.value.USER_LOGIN}</h3>
                    </div>

                    <p>
                        {error && <span className={'primary-color'}>{error}</span>}
                    </p>

                    <div className="form-group">
                        <input className="input" type="email"
                               placeholder={i18n.value.FORM_EMAIL}
                               {...register('email')}
                        />
                        <FieldErrorMessage error={errors['email']}/>
                    </div>

                    <div className="form-group">
                        <input className="input" type="password"
                               placeholder={i18n.value.ENTER_YOUR_PASSWORD}
                               {...register('password')}
                        />
                        <FieldErrorMessage error={errors['password']}/>
                    </div>

                    <p>
                        {i18n.value.NEED_ACCOUNT_QUESTION}&nbsp;
                        <Link to={uriService.uriRegister()}>{i18n.value.SIGN_UP_HERE}</Link>
                    </p>
                    <p>
                        <Link to={uriService.uriForgot()}>{i18n.value.FORGOT_QUESTION}</Link>
                    </p>

                </div>

                <div className="pull-right">

                    <PrimaryButton onClick={() => handleSubmit(onSubmit)} disabled={!isValid}>
                        {i18n.value.SUBMIT}
                    </PrimaryButton>
                </div>
            </form>
        </div>
    );
};

export {LoginForm}
