import {ApiKey, I18nLocale, UserLoginProps, UserType} from "../interface";
import {usersData} from "../data";

export const usersService = {
    users: [] as Array<UserType>,

    async _loadUsers () {
        if (!this.users.length) {
            const data: Array<UserType> = [];
            for (let i = 0; i < usersData.length; i++) {
                const item = usersData[i];
                data.push({...item});
            }
            this.users = data;
        }
    },

    async createUser(encode: I18nLocale, user: UserLoginProps): Promise<UserType> {
        await this._loadUsers();

        if (user.passwordConfirm !== user.password) {
            throw Error('Passwords are not equal!');
        }

        if (user.register) {
            // ... register AUTH
        }

        const result: UserType = {...user,
            id: String(this.users.length + 1),
            createdAt: new Date().toJSON(),
            changedAt: new Date().toJSON(),
        };

        this.users.push(result);

        return result;
    },

    async updateUser(encode: I18nLocale, id: ApiKey, user: UserLoginProps): Promise<UserType> {
        await this._loadUsers();

        const result = this.users.find(item => item.id === id);
        if (!result) {
            throw Error('User does not exists!');
        }

        if (user.passwordConfirm !== user.password) {
            throw Error('Passwords are not equal!');
        }

        if (user.register && user.password) {
            // change password AUTH
            result.password = user.password;
        }

        Object.assign(result, {...user,
            password: result.password,
            id: result.id,
            createdAt: result.createdAt,
            changedAt: new Date().toJSON(),
        });

        return result;
    },

    async loginUser(encode: I18nLocale, email: string, password: string): Promise<UserType> {
        await this._loadUsers();

        const result = this.users.find(item => item.email === email && item.password === password);

        if (!result) {
            throw Error('User or password are not valid!');
        }

        return {...result};
    },

}
